import { useContext, useRef, useState } from "react";
import { Contextapi } from "../App";
import "../styles.css";

export default function Captionbox() {
  const { data, setdata } = useContext(Contextapi);
  const [stime, setstime] = useState(0);
  const [etime, setetime] = useState(0);
  const [cap, setcap] = useState("");
  const capref = useRef();
  const startref = useRef();
  const endref = useRef();

  const addCaption = () => {
    const arr = [...data];
    for (let i = stime; i < etime; i++) {
      arr[i] = cap;
    }
    setdata(arr);
    // useRef.current.value = "";
    capref.current.value = "";
    startref.current.value = "";
    endref.current.value = "";
  };
  return (
    <div className="cap">
      <div className="cap-main">
        <div>
          <label>ENTER YOUR CAPTION</label>
        </div>
        <textarea
          name="Text1"
          cols="25"
          rows="3"
          maxLength="60"
          placeholder="Enter the text you ant to add.."
          onChange={(e) => {
            setcap(e.target.value);
            const arr = [...data];
            for (let i = stime; i < etime; i++) {
              arr[i] = capref.current.value;
            }
            setdata(arr);
          }}
          ref={capref}
        ></textarea>
      </div>
      <div className="tim">
        <div className="tim-main">
          <div className="tim-content">
            <div className="tim-label">
              <label>START</label>
            </div>
            <div className="tim-input">
              <input
                type="number"
                placeholder="0"
                onChange={(e) => setstime(e.target.value * 10)}
                ref={startref}
              />
            </div>
          </div>
        </div>
        <div className="tim-main">
          <div className="tim-content">
            <div className="tim-label">
              <label>END</label>
            </div>
            <div className="tim-input">
              <input
                type="number"
                placeholder="0"
                onChange={(e) => setetime(e.target.value * 10)}
                ref={endref}
              />
            </div>
          </div>
          <div className="tim-button">
            <button onClick={addCaption}>Add</button>
          </div>
        </div>
      </div>
    </div>
  );
}
