import { useRef, useEffect, useState, useContext } from "react";
import videojs from "video.js";
import { Contextapi } from "../App";
import "./video.css";

export default function Video() {
  const videoRef = useRef();
  const { data, setdata } = useContext(Contextapi);
  const [currentTime, setCurrentTime] = useState(null);
  const youtubeID = "https://www.w3schools.com/tags/movie.mp4";
  const videoJSOptions = {
    controls: true,
    userActions: { hotkeys: true },
    playbackRates: [0.5, 1, 1.5, 2]
  };

  useEffect(() => {
    if (videoRef) {
      const player = videojs(videoRef.current, videoJSOptions, () => {
        player.src(youtubeID);
        player.on("ended", () => {
          console.log("ended");
        });
        player.on("timeupdate", () => {
          setCurrentTime(player.currentTime());
        });
        console.log("Player Ready");
      });
    }
    return () => {};
  }, []);
  console.log(data);
  return (
    <div className="vid">
      <div className="addcap">{data[Math.floor(currentTime)]}</div>
      <video
        src="https://www.w3schools.com/tags/movie.mp4"
        controls="True"
        ref={videoRef}
        preload="none"
        className="video-js"
        style={{ width: "950px", height: "500px" }}
      />
    </div>
  );
}
